import { Builder } from './Builder'

export interface InputPaidMediaPhotoPayload {
    type?: 'photo'
    media: string
}

export interface InputPaidMediaVideoPayload {
    type?: 'video'
    media: string
    thumbnail?: InputPaidMedia | string
    width?: number
    height?: number
    duration?: number
    supports_streaming?: boolean
}

export class InputPaidMedia extends Builder {

    public constructor(...medias: InputPaidMediaType[]){
        super({ value: medias, parseVal: true })
    }
}

export type InputPaidMediaType = typeof PaidMedia.Photo|typeof PaidMedia.Video
export namespace PaidMedia {

    export function Photo(payload: InputPaidMediaPhotoPayload){
        return { type: 'photo', ...payload}
    }

    export function Video(payload: InputPaidMediaVideoPayload){
        return { type: 'video', ...payload }
    }

}